<template>
  <div>
   
   <transition
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter"
    >

        <div class="ball" v-if="flage" ref="ball" ></div>

    </transition>

   <transition
          
    >

        <div class="ball2" v-show="flage" ></div>

    </transition>

   
<!--  -->

      <div class="mui-card">
			<div class="mui-card-content">
			<div class="mui-card-content-inner">
            <mt-swipe :auto="4000" >
            <mt-swipe-item  v-for="item in lunbolist" :key="item.src"><img :src="item.src" alt=""></mt-swipe-item>
          </mt-swipe>
					</div>
				</div>
			</div>

<!--  -->
    
           <div class="mui-card">
				<div class="mui-card-header">{{canshu.title}}</div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
					
             <p class="jia"><span> <del > {{canshu.market_price}}</del> </span >   <span class="now">{{canshu.sell_price}}</span> </p>

             <div class="shuliang"> <span></span>  <num @getCount="getSelectedCount" :max='canshu.stock_quantity'></num></div>
               
       
                <div class="car">
                <button type="button" class="mui-btn mui-btn-primary"></button><button type="button" class="mui-btn mui-btn-danger" @click="addcar"></button></div>
					</div>
				</div>
		
			</div>

<!--  -->

<div class="mui-card">
				<div class="mui-card-header"></div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<p>{{canshu.goods_no}}</p>
                        <p>{{canshu.stock_quantity}}</p>
                        <p>{{canshu.add_time| dateFormat}}</p>
					</div>
				</div>
				<div class="mui-card-footer">
                    <button type="button" class="mui-btn mui-btn-primary mui-btn-outlined" @click='tuwen(id)'></button>
                    <button type="button" class="mui-btn mui-btn-danger mui-btn-outlined" @click='pinglun(id)'></button>
                    </div>
			</div>



  </div>
</template>
<script>
import mui from '../lib/mui/js/mui'
import num from './num'
export default {
  data() {
    return {
        id:this.$route.params.id,
        lunbolist:[],
        canshu:{},
          flage: false,
            SelectedCount:1//  
    }
  },
  components:{
    num
  },
  methods: {

getlunbo(){
    this.$http.get('api/getthumimages/'+this.id).then(result=>{
                  this.lunbolist=result.body.message
                 console.log(result.body)
           })
},


getcanshu(){
    this.$http.get('api/goods/getinfo/'+this.id).then(res=>{

    this.canshu=res.body.message[0]


    })
},
tuwen(id){

    this.$router.push({name:'shoptuwen',params:{id}})
},
pinglun(id){
     this.$router.push({name:'shopping',params:{id}})
},
addcar(){
   this.flage = !this.flage;

// storecar 
var goodsinfo ={
  id:this.id,
  count:this.SelectedCount,
  price:this.canshu.sell_price,
  selected:true
};

//storemutations
this.$store.commit('addToCar',goodsinfo)

},
 //el: js dom
            //document.getElementById() 
            beforeEnter:function (el) {
                //beforeEnter  beforeEnter
                //
                el.style.transform="translate(0,0)";

            },
            enter:function (el,done) {
                // 
                //el.offsetWidth

                el.offsetWidth
                //enter
          // 
            const ballPosition = this.$refs.ball.getBoundingClientRect();//
            const badgePosition = document.getElementById('badge').getBoundingClientRect();//
            const xDist = badgePosition.left - ballPosition.left;
            const yDist = badgePosition.top - ballPosition.top
            el.style.transform = `translate(${xDist}px,${yDist}px)`;
            el.style.transition = 'all 0.3s cubic-bezier(.4,-0.3,1,.68)';
              el.addEventListener('transitionend',done)
              // done()
 
            },
            afterEnter:function (el) {
                //
                this.flage=!this.flage;

            },


       getSelectedCount(count){
            //  data
            this.SelectedCount = count;

            console.log('' + this.SelectedCount)
           
        }

  },
     created() {
      this.getlunbo();
      this.getcanshu();
      
  },
  mounted() {
    mui(".mui-numbox").numbox();


  },
};
</script>
<style lang="scss" scoped>

p{margin-bottom: 0px;}
.mint-swipe{height: 200px;width: 100%;text-align: center;}
.mint-swipe img{height: 100%;}
.jia{font-size: 13px;color: gray; margin: 10px 0;
.now{color: red;font-weight: bold;}
}
.car{margin: 10px 0;
  button{margin: 0 5px;}
}
.mui-card-footer{
    display: block;
    button{width: 100%;margin: 10px 0;}
}
    .ball {
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background-color: red;
            z-index: 99;
            position: absolute;
            top: 400px;
            left: 143px;
      
  
        }
        .shuliang{display: flex;align-items: center;}
</style>




